* {
	font-family: "Roboto"; }
header {
	position: relative; }
nav {
	background-color: #212121;
	position: fixed;
	width: 100%;
	z-index: 200; }
.menu {
	padding: 10px 70px;
	height: 50px;
	list-style-type: none;
	margin: 0;
	display: flex;
	justify-content: space-between;
	align-items: center; }
.menu_link {
	font-weight: 300;
	font-size: 15px;
	line-height: 15px;
	color: #FFFFFF; }
.menu_item {}
.subheader {
	height: 84px;
	background-color: rgba(0, 0, 0, .6);
	position: absolute;
	width: 100%;
	margin-top: 50px; }
.subheader_row {
	height: 84px;
	display: flex;
	align-items: center; }
.subheader_logo {
	display: block; }
.logo_text {
	height: 13px;
	opacity: 0.5;
	color: #ffffff;
	font-size: 13px;
	font-weight: 100;
	margin-top: 6px; }
.subheader_call {
	color: #ffffff;
	font-size: 12px;
	font-weight: 300;
	line-height: 12px;
	text-align: right; }
.subheader_phone {
	color: #ffffff;
	font-size: 22px;
	font-weight: 400;
	text-align: center;
	text-transform: uppercase;
	margin-top: 8px; }
.subheader_btn {
	width: 172px;
	height: 46px;
	border: none;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	border-radius: 4px;
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
	color: #ffffff;
	font-size: 14px;
	font-weight: 400;
	line-height: 14px;
	text-align: center;
	text-transform: uppercase; }
.promo {
	min-height: 800px;
	background: url(../img/bg/bg_1.png) center center/cover no-repeat;
	padding: 201px 0 135px; }
.promo_header {
	color: #ffffff;
	font-weight: 900;
	text-align: center;
	text-transform: uppercase;
	font-size: 60px;
	line-height: 50px;
	margin: 0; }
.promo_subheader {
	color: #ffffff;
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	text-transform: uppercase;
	line-height: 36px;
	margin: 0;
	margin-top: 15px; }
.promo_descr {
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	line-height: 24px;
	text-align: center;
	margin-top: 23px; }
.promo_btn {
	width: 259px;
	height: 63px;
	border: none;
	display: block;
	margin: 0 auto;
	margin-top: 118px;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	border-radius: 4px;
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	line-height: 18px;
	text-align: center;
	text-transform: uppercase; }
.reasons {
	padding: 79px 0 65px; }
.label {
	width: 112px;
	height: 24px;
	margin: 0 auto;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-align: center;
	text-transform: uppercase;
	display: flex;
	align-items: center;
	justify-content: center; }
.reasons_row {
	padding-top: 40px; }
.reasons_item {
	display: flex;
	margin-top: 35px; }
.reasons_title {
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	line-height: 1;
	text-align: center;
	text-transform: uppercase;
	margin: 0 auto;
	margin-top: 38px; }
.reasons_icon {
	min-width: 113px;
	height: 113px;
	background-color: #1eacc7;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center; }
.reasons_text {
	margin-left: 26px; }
.reasons_subtitle {
	color: #1eacc7;
	font-size: 17px;
	font-weight: 400;
	line-height: 21px; }
.reasons_desr {
	color: #202020;
	font-size: 17px;
	font-weight: 300;
	line-height: 21px; }
